<!-- author: 刘思涵 -->
<template>
  <div id="aunt_list">
    <el-row>
      <el-col :xs="24" :sm="24" :md="12" :lg="12" :xl="8">
        <label class="filt-name">阿姨名称：</label>
        <input class="input-form" type="text" v-model="searchName" placeholder="输入阿姨名称">
      </el-col>
      <el-col :xs="24" :sm="24" :md="12" :lg="12" :xl="8">
        <label class="filt-name">手机号码：</label>
        <input class="input-form" type="text" v-model="searchPhone" placeholder="输入手机号码">
      </el-col>
      <el-col :sm="24" :lg="12" :xl="8">
        <el-button type="primary" @click="search">查询</el-button>
      </el-col>
    </el-row>
    <div class="table-wrap" ref="table">
      <div class="over-table">
        <span class="count">数据统计：共{{total}}个</span>
      </div>
      <el-scrollbar>
        <el-table :data="data" stripe border fit header-row-class-name="table-head">
        <el-table-column prop="id" label="ID"></el-table-column>
        <el-table-column prop="name" label="阿姨姓名"></el-table-column>
        <el-table-column prop="phone" label="手机号码">
        </el-table-column>
        <el-table-column label="阿姨头像">
          <template slot-scope="scope">
            <div>
              <img :src="scope.row.image" style="width:100%;height:90px"/>
            </div>
          </template>
        </el-table-column>
        <el-table-column label="录入时间">
          <template slot-scope="scope">
            <div>
              <p>{{scope.row.create_date}}</p>
            </div>
          </template>
        </el-table-column>
        <el-table-column label="服务类型" prop="type">
        </el-table-column>
        <el-table-column label="服务薪资" prop="money">
        </el-table-column>
         <el-table-column label="服务订单数" prop="num">
        </el-table-column>
        <el-table-column label="服务状态" prop="status">
        </el-table-column>
        <el-table-column label="操作">
          <template slot-scope="scope">
            <el-button @click="getDetail(scope.row)" type="text" size="small">查看详情</el-button>
            <el-button @click="appBlacklist(scope.row)" type="text" size="small">解除黑名单</el-button>
          </template>
        </el-table-column>
      </el-table>
      </el-scrollbar>
      <el-pagination background layout="prev, pager, next" :total="total" prev-text="上一页" next-text="下一页" @current-change="pageChange">
      </el-pagination>
    </div>
  </div>
</template>
<script>
import { getAuntListService } from '../../service/AuntManage'
export default {
  data () {
    return {
      activeName: 'first',
      searchName: '',
      searchPhone: '',
      data: [{
        type: '育婴师',
        money: '10000',
        num: '159034',
        phone: '15949084032',
        id: '1',
        image: 'http://b-ssl.duitang.com/uploads/item/201601/14/20160114163533_CV5tY.jpeg',
        company: '管家帮',
        status: '上户中',
        create_date: '2016-05-02',
        name: '王小虎'
      }, {
        type: '育婴师',
        money: '10000',
        image: 'http://b-ssl.duitang.com/uploads/item/201601/14/20160114163533_CV5tY.jpeg',
        num: '159034',
        status: '上户中',
        phone: '15949084032',
        id: '2',
        company: '管家帮',
        create_date: '2016-05-02',
        name: '王小虎'
      }, {
        type: '育婴师',
        money: '10000',
        status: '上户中',
        image: 'http://b-ssl.duitang.com/uploads/item/201601/14/20160114163533_CV5tY.jpeg',
        num: '159034',
        phone: '15949084032',
        id: '3',
        company: '管家帮',
        create_date: '2016-05-02',
        name: '王小虎'
      }],
      optionsType: [{
        value: '选项1',
        label: '母婴护理师'
      }, {
        value: '选项2',
        label: '育婴嫂'
      }, {
        value: '选项3',
        label: '育儿嫂'
      }, {
        value: '选项4',
        label: '家政工'
      }, {
        value: '选项5',
        label: '病人陪护'
      }, {
        value: '选项6',
        label: '老人陪护'
      }],
      typeValue: '',
      optionsStatus: [{
        value: '选项1',
        label: '上户中'
      }, {
        value: '选项2',
        label: '待岗'
      }],
      statusValue: '',
      optionsAffiliatedUnit: [{
        value: '选项1',
        label: '得贤家政'
      }, {
        value: '选项2',
        label: '爱家家政'
      }, {
        value: '选项3',
        label: '管家帮'
      }, {
        value: '选项4',
        label: '保姆家政'
      }],
      affiliatedUnitValue: '全部',
      page: 1,
      per_page: 10,
      total: 0
    }
  },
  created () {
    this._getAuntList()
  },
  methods: {
    handleClick () {
    },
    appBlacklist (obj) {
    },
    getDate (d) {
      this.start = this.dateToStr(new Date(d[0]))
      this.end = this.dateToStr(new Date(d[1]))
    },
    pageChange (page) {
      this.page = page
      this._getAuntList()
    },
    _getAuntList () {
      getAuntListService().then((res) => {
        console.log(res)
      })
    },
    dateToStr (date) {
      let y = date.getFullYear()
      let m = date.getMonth() + 1
      let d = date.getDate()
      m = m < 10 ? '0' + m : m
      d = d < 10 ? '0' + d : d
      return (y + '-' + m + '-' + d)
    },
    search () {
      this._getAuntList()
    },
    clear () {
      this.name = ''
      this.phone = ''
      this.statusValue = ''
      this.affiliatedUnitValue = ''
      this.typeValue = ''
      this._getAuntList()
    },
    getDetail (obj) {
      this.$router.push('/v2/AuntDetail/' + obj.id)
    },
    addAunt () {
      this.$router.push('/v2/createAunt/-1')
    }
  }
}
</script>
<style scoped>
.el-col{
  margin-bottom: 10px;
  text-align: left
}
.el-row{
  margin: 10px 0
}
.el-table >>> td, .el-table >>> th{
  text-align: center
}
.el-table >>> .table-head .cell{
  border-right: 1px solid #9FAFCC
}
.el-table >>> .table-head:last-child .cell{
  border-right: none
}
.el-table >>> .table-head th{
  background-color: #EEEEEE !important;
  border: none
}
.el-table >>> th {
  background-color: #EEEEEE;
  text-align: center;
}
.el-table >>> th  .cell {
  border-left: #a5a4a4 solid 1px;
  font-family: "苹方-简 常规体", "苹方-简 常规体", "苹方-简 常规体";
  font-weight: bolder;
  color: #333;
}
.el-table >>> th:first-child .cell {
  border-left: transparent solid 1px;
  font-family: "苹方-简 常规体", "苹方-简 常规体", "苹方-简 常规体";
  font-weight: bolder;
  color: #333;
}
.el-pagination{
  margin-top: 20px
}
.el-date-editor{
  width: 60%
}
.input-form{
  height: 35px;
  padding: 4px 10px;
  box-sizing: border-box;
  border-radius: 2px;
  border: 1px solid #dcdfe6;
  outline: none;
  width: 60%;
  font-size: 14px;
  border-radius: 5px;
  color: #333;
  /* background-color: #f9f9f9 */
}
.el-date-editor{
    border: 1px solid #B2B2B2;
    border-radius: 2px;
    background-color: #f9f9f9
}
.el-date-editor >>> .el-range-input{
    background-color: #f9f9f9
}
input::-webkit-input-placeholder {
  color: #999
}
input:-moz-placeholder {
  color: #999
}
input::-moz-placeholder {
  color: #999
}
input:-ms-input-placeholder {
  color: #999
}
.filt-name {
  margin-right: 20px;
  color: #666;
  font-weight: lighter
}
.table-wrap{
  padding-top: 20px;
  border-top: 1px solid #ccc
}
.over-table{
  overflow: hidden;
  margin-bottom: 20px
}
.count{
  height: 40px;
  line-height: 40px;
  padding: 0 30px;
  color: #fff;
  float: left
}
.btn-group{
