}
.el-pagination >>> li, .el-pagination >>> button{
  background-color: #fff !important;
  border: 1px solid #D6D6D6 !important
}
.el-pagination >>> li.active{
  background-color: #C12B20 !important
}
p{
  margin: 0
}
.el-dialog div{
  text-align: center
}
.el-dialog a{
  text-decoration: none;
  font-size: 20px;
  line-height: 44px;
  color: #666
}
.el-table {
  border: #B2B2B2 solid 0.5px;
}
.el-table >>> th {
  background-color: #EEEEEE;
  text-align: center;
}
.el-table >>> th  .cell {
  border-left: #a5a4a4 solid 1px;
  font-family: "苹方-简 常规体", "苹方-简 常规体", "苹方-简 常规体";
  font-weight: bolder;
  color: #333;
}
.el-table >>> th:first-child .cell {
  border-left: transparent solid 1px;
  font-family: "苹方-简 常规体", "苹方-简 常规体", "苹方-简 常规体";
  font-weight: bolder;
  color: #333;
}
#aunt_list{
  padding: 20px;
}
</style>



